
import Vue from 'vue'
import Vuex from 'vuex'

import state from './state'
import getters from './getters'
import mutations from './mutations'
import actions from './actions'


Vue.use(Vuex)

const store = new Vuex.Store({

  //start state
  state,

  //start getters

  // ging y nh computed  -  x l d liu trc khi tr v cc component
  getters,

  //start mutations

  // mutations -> cha cc method dng  thay i gi tr trong state --- nn vit theo kiu nh ny  d n d bo tr d qun l 
  mutations,

  //start action
  actions


})

export default store;


